	module.exports = {"data":{"markdownRemark":{"html":"<h1>Searching and Replacing in Vim</h1>\n<p>Search and replace in vim will search for all instances of a given text pattern and replace it with a string.</p>\n<h3>Command Keys</h3>\n<p>The commands used for search and replace:</p>\n<ul>\n<li><code>:substitute</code></li>\n<li><code>:s</code> (short abbreviated form of substitute)</li>\n</ul>\n<h3>Command Structure</h3>\n<p>The structure used for search and replace:</p>\n<p><code>:[range]</code> <code>s</code>/<code>[pattern]</code>/<code>[string]</code>/<code>[flags]</code> <code>[count]</code></p>\n<p>where…</p>\n<ul>\n<li><code>[range]</code> indicates the lines to search (e.g. <code>1</code>: first line, <code>$</code>: last line, <code>%</code>: all lines).</li>\n<li><code>[pattern]</code> is the text pattern to be searched.</li>\n<li><code>[string]</code> is the string that will replace the text pattern.</li>\n<li><code>[flags]</code> turn on additional search and replace options (e.g. <code>c</code>: confirm substitution, <code>g</code>: replace all occurences in each line, <code>i</code>: ignore case).</li>\n<li><code>[count]</code> replaces in <code>[count]</code> lines starting from the last line in <code>[range]</code> (or current line if <code>[range]</code> omitted).</li>\n</ul>\n<h3>Common Examples</h3>\n<p>Some common search and replace examples are listed below:</p>\n<ul>\n<li><code>:s/foo/bar/</code>\tChange the first ‘foo’ to ‘bar’ in the current line.</li>\n<li><code>:s/foo/bar/g</code>\tChange each ‘foo’ to ‘bar’ in the current line.</li>\n<li><code>:%s/foo/bar/g</code>\tChange each ‘foo’ to ‘bar’ in all the lines.</li>\n<li><code>:13s/foo/bar/g</code> Change each ‘foo’ to ‘bar’ in line 13.</li>\n<li><code>:%s/foo/bar/cgi</code> Change every ‘foo’ to ‘bar’ in all lines.</li>\n</ul>","fields":{"slug":"/vim/search-and-replace/"},"frontmatter":{"title":"Search and Replace"}}},"pathContext":{"slug":"/vim/search-and-replace/"}}

